import React, { Component } from 'react';
import './Experience.css';

export default class Experience extends Component {
  render() {
    return (
      <div className="exp-pg" id="exp-pg">
        <div className="section-title">
          <div className="section-tai">Experience</div>
        </div>
        <div className="exp-content">
          <div className="container">
          <div className="container" style={{ paddingTop: "30px" }}>
            <div className="exp-card paper">
              <div className="comp-title" style={{backgroundColor: "#76D7C4"}}>AAY Solutions</div>
              <div className="comp-role">Role: Assistant Developer</div>
              <div className="comp-stuff">
                <li>Work on freelance projects while getting mentored.</li>
                <li>IDEs: Visual Studio Code, Visual Studio, IntelliJ</li>
                <li>Game Engine(s): Unity</li>
                <li>Technologies used: NodeJS, ASP.NET Core II, Ruby On Rails</li>
              </div>
            </div>
          </div>  

            <div className="container" style={{ paddingTop: "30px", paddingBottom: "30px" }}>
              <div className="exp-card paper">
                <div className="comp-title" style={{backgroundColor: "#F5B041"}}>NineStars Group USA Inc</div>
                <div className="comp-role">Role: CFO Assistant</div>
                <div className="comp-stuff">
                  <li>Worked with all departments to finish daily tasks.</li>
                  <li>Worked with: NetSuite, SharePoint</li>
                  <li>Daily tasks:
                <ul>
                      <li>Enter/sync hundreds of item sales info into NetSuite</li>
                      <li>Call logistics departments of various retailers to ensure our products on-time delivery.</li>
                    </ul>
                  </li>
                </div>
              </div>
            </div>

            <div className="container" style={{ paddingBottom: "30px" }}>
              <div className="exp-card paper">
                <div className="comp-title">AnnZi International Co.</div>
                <div className="comp-role">Role: Customer Service Represenative, various</div>
                <div className="comp-stuff">
                  <li>Jan 2012-Dec 2016</li>
                  <li>Communicated with over 18,000 customers on shipping and product inquiries as well as complaints (both verbal and written)</li>
                  <li>Analyze financial reports</li>
                  <li>Customize reports utilized during tax filing for S Corporations</li>
                  <li>Managed inventory reports for over 1,000 products</li>
                </div>
              </div>
            </div>
          </div>
          </div>
        </div>
    );
  }
}


