import React, { Component } from 'react';
import './About.css';

export default class About extends Component {
  render() {
    return (
      <div className="about-pg" id="about-pg">
        <div className="section-title">
          <div className="section-tai">About me</div>
        </div>
        <div className="about-content">
          <div className="about-facts">
            <div className="about-quote">
              Hello, I'm Jenny- a software engineer. I'm a recent graduate from University of California, Santa Barbara (class of 2016) with a degree in Financial Mathematics and Statistics. My professional journey has taken me onto this path to develop software and games. I am a self-starter and a fellow gamer. ;D
            </div>
            <div>
              As a web and game developer, I have built products for many clients to their specifications and beyond. I'm currently located in Los Angeles, California. 
            </div>
          </div>
        </div>
      </div>
    );
  }
}


